body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper_numeracion{
  padding: 25px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content_subTotal,.content_Total{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  width: 350px;

}


label{
  font-size: 18px;
  font-weight: 600;
  padding: 0 10px;
  flex: 1;
  color: #bbb;
}
input{
  letter-spacing: 1px;
  outline: none;
  padding: 5px 15px;
  width: 200px;
  font-size: 15px;
  background: rgb(77, 77, 77);
  color: #fff;
  font-weight: 600;
  border-radius: 5px;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input[type=number] { -moz-appearance:textfield; }


.numero_inicial , .numero_final{
   
   margin: 10px 0;
   width: 350px;
   display: flex;
}


p{
  font-size: 15px;
  width: 200px;
  color: #ccc;
  text-align: left;
}

.total_of_numeration {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  background: #3f3f3fcc;
  margin: auto;
}

.content.lado_a , .content.lado_b {
  border-radius: 5px;
  background: #6f6f6f;
  margin: 15px;
  padding: 0 15px;
}


h2{
  margin: 0;
  font-size: 23px;
  color: #ddd;
}

.content_price{
  background: rgb(99, 99, 99);
  padding: 10px;
  width: 90%;
  display: flex;
  margin: 10px auto;
  border-radius: 5px;
}

@media screen and (max-width : 1300px) {

  .wrapper_numeracion{
    padding: 25px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
    
}


@media screen and (max-width : 400px) {

    
  input{
    width: 80%;
    flex: 2;
  }
  label{
    font-size: 13px;
  }
  .numero_inicial, .numero_final{
    width: 95%;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
  .content_subTotal, .content_Total{
    width: fit-content;
  }
  .total_of_numeration{
    width: 90%;
    margin: 15px auto;
  }
  p{
    width: fit-content;
    flex: 2;
  }

  .total_label{
    flex: 0.5;
  }

}